<template>
  <el-container class='login-wrap'>
    <el-aside class='aside-background' width='60%'></el-aside>
    <el-main class="main-background" style="padding: 0;">
      <div class="login-form-wrapper">
        <div class='ms-login'>
          <div class='ms-title'>
            <el-row>
              <img src="../../assets/images/Hilink-logo.png" alt="logo" title="瀚联云">
            </el-row>
            <el-row>
              <span>瀚联云</span>
            </el-row>
          </div>
          <el-form :model='form' :rules='rules' ref='form' label-width="50px" class="login-form">
            <el-form-item prop='username'>
              <el-input class="mini-height" v-model='form.username' :disabled='isLogin' placeholder='用户名' clearable></el-input>
            </el-form-item>
            <el-form-item prop='password'>
              <el-input class="mini-height" type='password' placeholder='密码' :disabled='isLogin' v-model='form.password' @keyup.enter.native='_login' clearable></el-input>
            </el-form-item>
            <el-form-item prop='checkCode' style="margin-bottom: 0;">
              <el-col :span="12">
                <el-input class="mini-height" placeholder='请输入验证码' :disabled='isLogin' v-model='form.checkCode' clearable></el-input>
              </el-col>
              <el-col :span="11" :offset="1">
                <img-verify @imgCode="imgCode" ref="imgVerify"/>
              </el-col>
            </el-form-item>
          </el-form>
          <el-row class='login-btn' :gutter="0" type="flex" justify="flex-start">
            <el-col :span="12">
              <el-button type='primary' @click='_login' :loading='isLogin'>{{isLogin ? '登录中...' : '登录'}}</el-button>
            </el-col>
            <!-- <el-col :span="12">
              <el-button type='default' @click='_login' :loading='isLogin'>注册</el-button>
            </el-col> -->
          </el-row>
          <!-- <el-row class="to-forget-pass">
            <router-link to="/a">忘记密码</router-link>
          </el-row> -->
          <footer>Copyright(C) 2015-2017 南京瀚联 苏ICP备15018616号-2</footer>
        </div>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import { Button, Form, FormItem, Input } from 'element-ui'
import { mapActions, mapGetters } from 'vuex'
import imgVerify from '@/components/v-img-verify'

export default {
  components: {
    'el-button': Button,
    'el-form': Form,
    'el-form-item': FormItem,
    'el-input': Input,
    'img-verify': imgVerify
  },
  computed: {
    ...mapGetters(['User'])
  },
  data () {
    return {
      isLogin: false,
      form: {
        username:
          process.env.NODE_ENV === 'development'
            ? 'admin'
            : localStorage.username ? localStorage.username : '',
        password: process.env.NODE_ENV === 'development' ? '123' : '',
        checkCode: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    ...mapActions({
      login: 'common/loginSystem',
      getUser: 'common/getUser'
    }),
    _login () {
      this.isLogin = true
      /** 登陆后获取接口
       *   1. 批次列表  2.操作员信息 3.用户信息
       */
      return (async () => {
        try {
          await this.login(this.form)
          this.isLogin = false
          this.$router.push({
            name: 'monitor'
          })
        } catch (err) {
          this.isLogin = false
        }
      })()
    },
    imgCode (code) {
      console.log(code)
    },
    handleClick () {
      this.$refs.imgVerify.draw()
    }
  }
}
</script>
<style lang="less" scoped>
.login-wrap {
  position: relative;
  width: 100%;
  height: 100%;
  .aside-background {
    background: url(../../assets/images/login-left.jpg);
    zoom: 1;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center 0;
  }
  .main-background {
    background: url(../../assets/images/login-right.jpg);
    zoom: 1;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center 0;
  }
  .login-form-wrapper {
    position: relative;
    width: 30rem; // 180px
    text-align: center;
    top: 50%;
    margin-top: -10.0416rem;
    .ms-login {
      width: 30rem; // 360px
      .ms-title {
        font-size: 2.5rem;
        width: 100%;
        img {
          background-color: #ccc;
        }
      }
    }
    .login-btn {
      margin-left: 2.0833rem;
      button {
        width: 50%;
        height: 3rem;
        line-height: 0;
      }
    }
    footer {
      margin-top: 3rem;
      height: 4.1667rem; // 50px
      font-size: 1.1667rem; // 14px;
      color: #aebdc9;
    }
  }
}

</style>



